import "./consiglio.js";
import { StringRenderer } from "../../commons/js/string_renderer.js";

$(document).ready(function () {

    var urlParams = new URLSearchParams(window.location.search);
    if (urlParams.has("detail_obj_id") && urlParams.has("ref_type_id")) {
        var url_to_call = apibase.baseurl + urlParams.get("ref_type_id") + "/" + urlParams.get("detail_obj_id");
        $.ajax({
            url: url_to_call, success: function (result_api) {
                if (result_api != null) {
                    console.log('result detail: ', result_api);

                    $("#obj_tabs_nav").hide();
                    $("#nav-tabContent").hide();
                    $("#obj_anagrafica").hide();
                    $("#headingLEG").hide();
                    let stringedDate = '';
                    if (result_api.properties.data_di_pubblicazione) {
                        stringedDate = 'Pubblicato il ' + StringRenderer.renderDataTestuale(result_api.properties.data_di_pubblicazione);
                    } else {
                        stringedDate = '';
                    }
                    if (result_api.properties.data_di_scadenza) {
                        stringedDate += '<br/> Scade il ' + StringRenderer.renderDataTestuale(result_api.properties.data_di_scadenza);
                        if (result_api.properties.data_di_scadenza_time) {
                            stringedDate += ' alle ' + result_api.properties.data_di_scadenza_time.slice(0, 5);
                        }
                    }
                    $("#title").text(result_api.title);
                    let intestazione = result_api.properties.sottotitolo;
                    if (intestazione == undefined) {
                        intestazione = "";
                    }
                    var htmlDate = '<p>' + intestazione + '</p> <p ><strong>' + stringedDate + '</strong></p ></div>';
                    if (result_api.components.immagine != undefined) {
                        if (result_api.components.immagine.components.length > 0) {
                            $("#container_base").append("</div>" + "<div class=\"row\">\n" +
                                "<div class=\"col-3 \" id=\"row_immagine\">\n" +
                                "\n" +
                                "</div> <div class='col-9' id='col_corpo'></div> \n" +
                                "</div>");
                            result_api.components.immagine.components.forEach(function (cartella) {
                                var content = '<img id="img_pagina" class="img_elenco" alt="' + cartella.title + '" title="' + cartella.title + '" /> ';
                                $("#row_immagine").append(content);

                                if (cartella.components.file != undefined) {
                                    if (cartella.components.file.components.length > 0) {
                                        let file = cartella.components.file.components[0];
                                        var iconLink = baseImgUrl + file.properties.address;
                                        var thumb = baseImgUrl + file.properties.thumbadress;
                                        $("#img_pagina").attr('src', iconLink);
                                        $("#img_pagina").attr('data-src', thumb);
                                    }

                                } else {
                                    var iconLink = "assets/consigliere_colore.svg";
                                    $("#img_pagina").attr('src', iconLink);
                                    $("#img_pagina").attr('data-src', iconLink);
                                }
                            });
                            $("#col_corpo").append(htmlDate + result_api.properties.descrizione)
                        } else {
                            $("#container_base").append(htmlDate + result_api.properties.descrizione)
                        }
                    } else {
                        $("#container_base").append(htmlDate + result_api.properties.descrizione)

                    }


                    if (result_api.components.allegati != undefined) {
                        if (result_api.components.allegati.components.length > 0) {
                            $("#container_base").append("<hr class=\"border-news mb-0\"/>" + "<div class=\"row\">\n" +
                                "<h6 class=\"dark-obj-name ml-2\">" + result_api.components.allegati.label + "</h6>" +
                                "<div class=\"container \" id=\"row_allegato\">\n" +
                                "\n" +
                                "</div>\n" +
                                "</div>");
                            result_api.components.allegati.components.forEach(function (cartella) {
                                let content = '<div class="row  bg-title-area m-2"  ><div class="col"> <a href="' + baseImgUrl + cartella.properties.address + '" target="_blank"> ' + cartella.title + ' </a></div></div>';
                                $("#row_allegato").append(content);
                            });
                        }


                    }
                    if (result_api.components.link != undefined) {
                        if (result_api.components.link.components.length > 0) {
                            $("#container_base").append("<hr class=\"border-news mb-0\"/>" + "<div class=\"row\">\n" +
                                "<h6 class=\"dark-obj-name ml-2\">" + result_api.components.link.label + "</h6>" +
                                "<div class=\"container \" id=\"row_links\">\n" +
                                "\n" +
                                "</div>\n" +
                                "</div>");

                            result_api.components.link.components.forEach(function (cartella) {
                                var content = "";
                                content += '<div class="row  m-2"  ><div class="col"> <a href="' + cartella.properties.url + '" target="_blank"> ' + cartella.title + ' </a></div></div>';

                                $("#row_links").append(content);
                            });
                        }


                    }
                    let galleria_immagini = result_api.components.galleria;
                    if (galleria_immagini) {
                        let htmlMultimedia = "";
                        for (var rr = 0; rr < galleria_immagini.components.length; rr++) {
                            var subGallery = galleria_immagini.components[rr].components.file;
                            htmlMultimedia += '<hr class="border-news mb-0"/> <div  class="row" id="row_gallery"> <h6 class="dark-obj-name ml-2"> <a  data-toggle="collapse" href="#collapseImg' + rr + '" role="button" aria-expanded="true" aria-controls="collapseImg' + rr + '"> ' + galleria_immagini.label + ' </a></h6> </div>';

                            console.log('galleria ' + rr, subGallery);
                            htmlMultimedia += '<div id="collapseImg' + rr + '" class="row collapse"> <div class="col-12"><h6>' + galleria_immagini.components[rr].title + '</h6></div>';
                            for (var kk = 0; kk < subGallery.components.length; kk++) {
                                let singolaImg = subGallery.components[kk];
                                var link = baseImgUrl + singolaImg.properties.address;
                                var iconLinkImg = baseImgUrl + singolaImg.properties.thumbaddress;
                                var label_title_grid = singolaImg.title;
                                var gruppo = "";
                                htmlMultimedia += '<div class="col-6 col-sm-4  align-content-center" > <a href="' + link + '" target="_blank" class="dark-obj-name-bold"><div class =" text-center"><img alt="' + singolaImg.title + '" title="' + singolaImg.title + '" class="img_elenco" src="' + iconLinkImg + '" /> </div> <p class="dark-obj-name-bold mt-2 mb-0 text-center">' + label_title_grid + '</p> </a> <p class="little-font-size mt-0 ml-4 mb-5"> ' + gruppo + '  </p>  </div>';
                            }
                        }
                        htmlMultimedia += "</div>";
                        $("#container_base").append(htmlMultimedia);
                    }
                }
            }
        });
    } else {
        window.location = "notFound.html";
    }


});